import React from 'react'
import PropTypes from 'prop-types'
import { Form, Input, Button } from 'antd';
import { toast } from "react-toastify";
import { login, register } from "../service/fetch";
import { connect } from 'react-redux'
import { changeLoginStatus, changeUserType, setLikes } from '../reducers/common'

class LoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            btnText: '',
        }
    }
    static propTypes = {
        type: PropTypes.number, // 0 , 1 
    }
    componentDidMount() {
        this.setState({
            btnText: this.props.type === 0 ? ' ' : ' '
        })
    }

    handleResult(result) {
        const { data } = result;
        console.log('data===', data);
        
        if (data) {
            window.sessionStorage.setItem('type', data.user.type);
            window.sessionStorage.setItem('user', `${data.user.source}${data.user.name}`);
            toast.dismiss();
            this.props.changeLoginStatus(true); 
            this.props.changeUserType(data.user.type === 1);
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type === 0) {
                    register(values).then(result => {
                        this.handleResult(result);
                    });
                } else {
                    login(values).then(result => {
                        this.handleResult(result);
                    });
                }
            }
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                { this.props.type === 0 && 
                    <div>
                        <Form.Item label="">
                        {getFieldDecorator('source', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input placeholder="" />
                        )}
                    </Form.Item>
                    <Form.Item label="">
                        {getFieldDecorator('name', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input placeholder="" />
                        )}
                </Form.Item>        
                    </div>
                }
                <Form.Item label="">
                    {getFieldDecorator('email', {
                        rules: [{ required: true, message: '' }],
                    })(
                        <Input placeholder="()" />
                    )}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('password', {
                        rules: [{ required: true, message: '' }],
                    })(
                        <Input type="password" placeholder="" />
                    )}
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">{ this.state.btnText }</Button>
                </Form.Item>
            </Form>
        );
    }
}

const loginToast = Form.create({ name: 'normal_login' })(LoginForm);

const mapStateToProps = (state) => {
    return {
        isLogined: state.commonReducer.isLogined,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        changeLoginStatus: (isLogin) => {
            dispatch(changeLoginStatus(isLogin));
        },
        changeUserType: (isAdmin) => {
            dispatch(changeUserType(isAdmin));
        },
        setLikes: (likes) => {
            dispatch(setLikes(likes));
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(loginToast)
